        "enunciado": "Um pilar importante do cuidado peditrico  a preveno. Os testes de triagem visam rastrear doenas antes que elas se apresentem clinicamente, com a finalidade de permitir uma interveno oportuna, evitando ou minimizando os efeitos da evoluo natural do agravo. Sobre a preveno no cuidado peditrico, assinale a alternativa falsa:",
