        "imagem_a": "Imagens/",
        "enunciado_b": "O \"teste da orelhinha\" (triagem auditiva neonatal) deve ser realizada em todos os recm-nascidos internados em UTI antes da alta hospitalar, e no apenas naqueles com indicativos de alto risco.",
        "imagem_b": "Imagens/",
