        "enunciado": "Um neonato do sexo masculino, nascido com 37 semanas e pesando 3 kg, amamentado exclusivamente ao seio, iniciou ictercia aos oito dias de vida. Com 16 dias de vida, foi levado ao ambulatrio em bom estado geral, pesando 3.220 g e com eliminaes gastrintestinais normais. Excetuando-se a ictercia, seu exame fsico era normal. Nessa ocasio, possuia aumento de bilirrubina s custas de indireta, cuja dosagem srica foi 12 mg/dl. Seu tipo sanguneo era A Rh positivo e o de sua me O Rh positivo. Seu hemograma e sua contagem de reticulcitos foram normais e o teste de Coombs direto foi negativo. O teste do pezinho tambm era normal. O menino foi acompanhado ambulatorialmente e aos dois meses no estava mais ictrico. A melhor hiptese diagnstica  ictercia:",
