        "enunciado": "A me de um lactente de 3 meses, primeiro filho, durante a consulta de puericultura, questiona ao mdico se j  oportuna a transio do leite materno para outros alimentos e a necessidade de oferta de gua e chs. Qual a melhor orientao nutricional para esse beb?",
