        "correta": "a",
        "dica": "",
        "comentario": "Primeiro Perodo: Dilatao do colo uterino de 1 a 10 cm. Segundo Perodo: Expulso do Concepto que ocorre quando o colo est com 10 cm de dilatao. Terceiro Perodo: Delivramento ou Expulso da Placenta, geralmente, aps a expulso do concepto. Quarto Perodo: Perodo de Greenberg que equivale at 1 hora depois do parto onde deve ser observado o volume de sangue eliminado via vaginal (REZENDE: Obstetrcia Fundamental. 11 ed. Guanabara Koogan. 2011. RJ)."
