        "imagem_a": "Imagens/",
        "enunciado_b": "Que  necessrio primeiro realizar a prova de coombs indireto da me. Caso este seja negativo, administrar a vacina at 72 horas aps o parto. ",
        "imagem_b": "Imagens/",
        "enunciado_c": "Que  necessrio primeiro realizar a prova de coombs indireta do filho, retirando uma amostra de sangue pelo cordo umbilical (cordocentese).",
