        "imagem_e": "Imagens/",
        "correta": "b",
        "dica": "",
        "comentario": "Para a administrao da RHOGAN/ MATHERGAN  necessrio a solicitao do Teste de Coombs Indireto Materno. Caso o Coombs Indireto Materno seja negativo, a me no foi sensibilizada pelo Fator Rh positivo do primeiro filho e no produz Anti-Rh, podendo tomar a vacina at 72 horas ps-parto. Para confirmar a real necessidade da vacina na gravidez do segundo filho, faz-se o Coombs Direto do Rn que, dando negativo,  sinnimo de que ele no foi atacado por anticorpos anti-Rh, confirmando tambm que a me no produziu estes anticorpos. "
