        "enunciado": "A ictercia constitui-se em um dos problemas mais frequentes no perodo neonatal e corresponde  expresso clnica da hiperbilirrubinemia. A hiperbilirrubinemia na primeira semana de vida  encontrada tanto no recm-nascido a termo como no prematuro. Diante de um prematuro de 36 semanas de idade gestacional, existem fatores epidemiolgicos que orientam o neonatologista para uma investigao clnica e laboratorial. Quais das situaes abaixo significam alta probabilidade da ictercia ter origem patolgica?",
