        "grupo_id": 8,
        "enunciado_a": "A bilirrubina  encontrada no soro normal de mamferos sendo o produto de degradao, principalmente, da hemoglobina liberada pelos eritrcitos que atingem o termo de sua vida podendo aparecer uma hora aps a destruio do glbulo vermelho ",
