        "enunciado_b": "A ictercia prpria do recm-nascido (ou ictercia fisiolgica)  uma condio clnica em geral benigna e reversvel",
        "imagem_b": "Imagens/",
        "enunciado_c": "Os nveis sricos de bilirrubina se correlacionam com a intensidade da cor amarelada da pele e com a progresso crnio-caudal da ictercia",
