        "enunciado_a": "O perodo de latncia  definido como perodo de tempo decorrido entre o incio da perda de lquido e a evoluo do quadro clnico para corionamnionite.",
        "imagem_a": "Imagens/",
        "enunciado_b": "O objetivo da antibioticoterapia  manter a gestao at o termo",
        "imagem_b": "Imagens/",
        "enunciado_c": "A inibio da contratilidade uterina deve sempre ser opo diante de trabalho de parto franco.",
        "imagem_c": "Imagens/",
        "enunciado_d": "O toque vaginal deve ser evitado, s sendo justificado nas gestaes com indicao de interrupo ou em trabalho de parto.",
        "imagem_d": "Imagens/",
        "enunciado_e": "",
