        "grupo_id": 5,
        "enunciado_a": "Fase Ativa Prolongada",
        "imagem_a": "Imagens/",
        "enunciado_b": "Parto Precipitado",
        "imagem_b": "Imagens/",
        "enunciado_c": "Parada Secundria de Dilatao",
        "imagem_c": "Imagens/",
        "enunciado_d": "Perodo Plvico Prolongado",
        "imagem_d": "Imagens/",
        "enunciado_e": "",
