        "imagem_a": "Imagens/",
        "enunciado_b": "A palpao, por meio do toque vaginal, da fontanela anterior determina que a flexo do polo ceflico ocorreu.",
        "imagem_b": "Imagens/",
        "enunciado_c": "A amplitude de rotao interna pode variar por volta de 45 nas variedades de posio occiptoanteriores e por volta de 135 nas variedades de posio occiptoposteriores.",
        "imagem_c": "Imagens/",
        "enunciado_d": "Na rotao externa, a cabea fetal gira, assumindo a posio contrria quela que estava quando do encaixamento.",
