        "imagem_a": "Imagens/",
        "enunciado_b": "Ser introduzida a partir do sexto ms de vida e conter cereais ou tubrculos, legumes, verduras, leguminosas e protena animal.",
        "imagem_b": "Imagens/",
        "enunciado_c": "Ser introduzida a partir do sexto ms de vida e conter cereais ou tubrculos, legumes, verduras e leguminosas.",
        "imagem_c": "Imagens/",
        "enunciado_d": "Ser introduzida a partir do oitavo ms de vida e conter cereais ou tubrculos, legumes, verduras e leguminosas.",
        "imagem_d": "Imagens/",
