        "enunciado_a": "Peso para estatura, IMC para idade, prega tricipital e permetro ceflico.",
        "imagem_a": "Imagens/",
        "enunciado_b": "Peso para idade, peso para estatura, estatura para idade e IMC para idade.",
        "imagem_b": "Imagens/",
        "enunciado_c": "Peso para idade, estatura para idade, circunferncia braquial e permetro ceflico.",
        "imagem_c": "Imagens/",
