        "enunciado": "A queixa de \"pouco leite\" ou \"leite fraco\" deve ser sempre valorizada pois, se no valorizada, pode levar ao desmame precoce. Marque a alternativa que apresenta orientao incorreta quanto  amamentao:",
        "img_url": "Imagens/",
