        "enunciado_a": "Melhorar o posicionamento e a pega do beb, quando no adequados.",
        "imagem_a": "Imagens/",
        "enunciado_b": "Oferecer as 2 mamas em cada mamada.",
        "imagem_b": "Imagens/",
