        "enunciado": "Em relao ao leite materno e ao aleitamento, a alternativa correta :",
        "img_url": "Imagens/",
        "nivel_id": 3,
        "grupo_id": 7,
        "enunciado_a": "A principal protena na composio do leite materno  a casena.",
        "imagem_a": "Imagens/",
        "enunciado_b": "A pega  considerada adequada quando a boca do beb est bem aberta, grande parte da arola est inserida dentro da boca do beb, o lbio inferior est virado para fora e o queixo toca a mama.",
        "imagem_b": "Imagens/",
        "enunciado_c": "O leite materno pode ser armazanado na geladeira por 24 horas e no congelador por 30 dias.",
        "imagem_c": "Imagens/",
        "enunciado_d": "O leite humano contm anticorpos contra bactrias e vrus, com predomnio de IgM, que impede a aderncia de micro-organismos  mucosa intestinal.",
