        "enunciado": "O \"teste do reflexo vermelho\"  uma ferramenta de rastreamento de alteraes que possam comprometer a transparncia dos meios oculares e, segundo o Ministrio da Sade, deve ser realizado:",
        "img_url": "Imagens/",
